  height: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.search-form {
  z-index: 999;
  width: 100%;
  background-image: linear-gradient(to right, #008, #404, #020275);
  justify-content: center;
  padding: 10px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form .search-form__input-wrapper {
  position: relative;
}

.search-form__input {
  height: 35px;
  max-width: 300px;
  width: 100%;
  border: 0;
  border-radius: 15px 0 0 15px;
  outline: none;
  padding: 5px 20px;
  font-size: 15px;
}

.search-form__btn {
  width: 50px;
  height: 35px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  padding: 7px 8px 5px 5px;
}

.search-form__icon {
  fill: #fff;
  transition: transform .35s;
}

.search-form__btn:hover .search-form__icon {
  transform: scale(.9);
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  gap: 15px;
  padding: 60px 0 100px;
  display: grid;
}

.photo-card-link {
  color: #000;
  border-radius: 3px;
  text-decoration: none;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 0 1px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 0 1px rgba(0, 0, 0, .2);
}

.photo-card-link:hover, .photo-card-link:visited, .photo-card-link:active {
  text-decoration: none;
}

.photo-card-link:hover, .photo-card-link:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.photo-card {
  max-height: 300px;
  height: 100%;
  flex-direction: column;
  display: flex;
}

.photo-card-img {
  max-height: 200px;
  height: 100%;
  object-fit: cover;
}

.info {
  justify-content: space-around;
  padding: 15px 0 5px;
  display: flex;
}

.info .info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  font-size: 12px;
  display: flex;
}

@media screen and (min-width: 425px) {
  .info .info-item {
    font-size: 14px;
  }
}

.loader {
  width: 45px;
  height: 45px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  border: 2px solid rgba(0, 0, 255, .6);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  transition: all .15s;
  animation: rotate 11s linear infinite;
  display: flex;
  position: fixed;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.loader.visible {
  opacity: 1;
  visibility: visible;
}

.spinner {
  width: 27px;
  height: 27px;
  background-color: rgba(0, 0, 0, 0);
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: #7006ad rgba(0, 0, 0, 0) #00f;
  border-radius: 50%;
  transition: opacity .1s;
  animation: rotate 1s linear infinite;
}

